import React from 'react';
import { Email, Td, Tr, Table, Span, A, renderEmail, Image, configStyleValidator } from 'react-email-template'
import Header from "../component/Header"

const css = `
@media only screen and (max-device-width: 480px) {
  font-size: 20px !important;
}`.trim()

configStyleValidator({
  // When strict, incompatible style properties will result in an error.
  strict: true,

  // Whether to warn when compatibility notes for a style property exist.
  warn: true,

  // Platforms to consider for compatibility checks.
  platforms: [
    'gmail',
    'gmail-android',
    'apple-mail',
    'apple-ios',
    'yahoo-mail',
    'outlook',
    'outlook-legacy',
    'outlook-web',
  ],
})



const Home = () => {
  // console.log(cardImage);

  function download(filename, text) {
    var element = document.createElement('a');
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
    element.setAttribute('download', filename);

    element.style.display = 'none';
    document.body.appendChild(element);

    element.click();

    document.body.removeChild(element);
  }

  return (
    <div className="">
      {Template}
      <div className="" onClick={() => download("index.html", renderEmail(Template))}>Download file</div>
    </div>

  );
};




const sourceURL = "https://image.prepladder.com/content/pD5fkt4u7OD73leKLzje1594984655.png"

const styles = {
  heading: {
    textAlign: 'center',
    fontSize: '16px'
  },
  textCenter: {
    textAlign: 'center'
  }
}

const Template = (
  <Email title="Test Email" headCSS={css} bodyStyle={{background:"#d0d0d0"}} bgcolor="#d0d0d0" style={{background:"#fff"}}>
    <Header />
    <Tr>
      <Td>
        <Span fontSize={15} style={{ ...styles.heading, ...styles.textCenter, color: 'red' }}>Hello, world!</Span>
      </Td>
    </Tr>

    <Tr>
      <Td>
        <Table cellSpacing={20} width="100%" style={{ borderTop: '3px solid black' }}>
          <Tr>
            <Td>
              <Span color="gray" lineHeight={20}>Generated by <A href="https://github.com/chromakode/react-html-email">react-html-email</A></Span>
              <Image data-mc-bar="bar" data-mc-baz="baz" alt="react" src={sourceURL} height={100} />
              <A download={sourceURL} href={sourceURL}>Source code</A>
            </Td>
          </Tr>
        </Table>
      </Td>
    </Tr>

  </Email>
)

// console.log(renderEmail(Home));

export default Home;